<template>
    <div class="user-promote-order">
        <van-tabs  :active="active" line-width="40" swipe-threshold="4" @change="onChange">
            <van-tab v-for="(item, index) in order" :title="item.name" :name="item.type" :key="index">
                <promote-order class="mt10" v-if="item.isShow" :order-type="item.type" :id="item.type"></promote-order>
            </van-tab>
        </van-tabs>
    </div>
</template>

<script>
import {userOrderPromoteOrder} from '@U/type'
import PromoteOrder from "@C/PromoteOrder"
export default {
    name: "userPromoteOrder",
    components: {
        PromoteOrder
    },
    data() {
        return {
            order: [
                {
                    name: "全部",
                    type: userOrderPromoteOrder.ALL,
                    isShow: false,
                },
                {
                    name: "待返佣",
                    type: userOrderPromoteOrder.WAIT_RETURN,
                    isShow: false,
                },
                {
                    name: "已结算",
                    type: userOrderPromoteOrder.HANDLED,
                    isShow: false,
                },
                {
                    name: "已失效",
                    type: userOrderPromoteOrder.INVALED,
                    isShow: false,
                }
            ],
            active: userOrderPromoteOrder.ALL,
        }
    },
    created() {
        let type = this.$route.query.type || userOrderPromoteOrder.ALL
		this.changeShow(type)
    },
    methods: {
        changeShow(type) {
            const { order } = this
            let index = order.findIndex((item) => {
                return item.type == type;
            });
            if (index != -1) {
                this.active = type
                this.order[index].isShow = true;
            }
        },
        onChange(e) {
            this.changeShow(e);
        }
    }
}
</script>

